<template>
 <div class="sureorder">
    <Mheader></Mheader>
    <h1 class="mw">确认订单</h1>
    <div class="mw content">
        <div class="sureorder-box">
            <p class="order-times">下单时间：<span>2018-08-08 08:08</span></p>
            <div class="course-item">
              <ul>
                <li v-for="(item,index) in items" :key="index">
                  <div class="course-img fl"><img src="../assets/web.png" alt=""></div>
                  <div class="course-info fl">
                    <p class="course-name">{{item.courseneme}}</p>
                    <p class="teachers-name">{{item.teachersneme}}</p>
                    <p class="videos-times">{{item.videostimes}}</p>
                  </div>
                  <div class="course-price fl">￥{{item.courseprice}}</div>
                </li>
              </ul>
            </div>
            <div class="pay-box ov">
              <div class="fl payinfo">
                <p class="number">共1件课程</p> 
                <p class="amount">商品总金额：<span>￥{{tocalprice}}</span></p>
              </div>
              <!-- <div class="fl wechat">
                <button @click="payment">选择支付方式</button>
              </div> -->
              <div class="fr alipay">
                <button @click="alipay(0)">支付宝支付</button>
              </div>
              <div class="fl wechat">
                <button @click="wechatpay(1)">微信支付</button>
              </div>
            </div>
              <p class="notice"><i class="iconfont icon-gantanhao"></i>请在22小时30分钟 以内支付完成，如未完成此订单将自动关闭。需重新购买！</p>
        </div>
    </div>

    <div class="alipay-dialog" v-show="isShow">
      <div class="dialog_bg"></div>
      <div class="dialog-container">
        <div class="alipay-navbar">
          <div class="dialog-navbar fl">
            <ul>
              <li class="cursor" @click="tabs(n)" :class="{active:n==num}" v-for="(alipay,n) in alipays" :key="n">{{alipay}}</li>
            </ul>
          </div>
          <div class="CloseBtn fr" @click="CloseDialog"><i class="iconfont icon-delete"></i></div>
        </div>
        <div class="payment-container">
          <ul>
            <li v-for="(tabitems,n) in tabcontents" :key="n" v-show="n==num">
              <div class="wechat-box">
               <img :src="tabitems.imgs" alt="">
              </div>
              <p class="price">付款金额：<span>￥{{tocalprice}}</span></p>
              <p :class="tabitems.color">{{tabitems.payment}}扫码支付</p>
            </li>
          </ul>
        </div> 
      </div>
   </div>
 </div>
</template>

<script type="text/javascript">
import Mheader from '@/components/Mheader'

  export default {
   data() {
     return {
       isShow: false,
       num:0,
       tocalprice:299.9,
       alipays:[
           "支付宝付款","微信付款"
       ],
       tabcontents:[
         {
           imgs:'/static/img/wechat.07ef597.png',
           payment:'支付宝',
           color: 'AlypayText'
         },
         {
           imgs:'/static/img/aliypay.2afe485.png',
           payment:'微信',
           color: 'WechatText'
         }
       ],
       items:[
        {
           courseneme:'全网最热Python3入门+进阶 更快上手实际开发',
           teachersneme:'请叫我小任性',
           videostimes:'视频时长：一个小时20分',
           courseprice:333.33
        },
        {
           courseneme:'全网最热Python3入门+进阶 更快上手实际开发',
           teachersneme:'请叫我小任性',
           videostimes:'视频时长：一个小时20分',
           courseprice:333.33
        }
       ]
      }
  },
  methods:{
    tabs(index){
      this.num = index;
      console.log(index)
    },
    // payment(){
    //   this.isShow = true;
    // },
     CloseDialog(){
        this.isShow = false;
      },
      wechatpay(index){
        this.isShow = true;
        this.num = index;
      },
      alipay(index){
       this.isShow = true;
       this.num = index;
      },
  },
   components: {
     Mheader
   }
 }
</script>

<style scoped lang="less">
.mw{
    width: 1000px;
    margin: 45px auto;
}
h1{
  text-align: left;
}
i{
  margin-right: 6px;
  vertical-align: middle;
}
button{
  border: none;
  color: #fff;
  font-size: 12px;
  width: 160px;
  height: 34px;
  line-height: 34px;
  text-align: center;
  border-radius: 4px;
  cursor: pointer;
}
.content{
  background: #fff;
  box-shadow: 0 2px 8px 2px rgba(0, 0, 0, 0.1);
  padding: 20px 30px 60px 30px;
  }

.sureorder-box{
    .order-times{
      text-align: left;
      margin-bottom: 16px;
    }
    .course-item{
      ul{
        li{
          overflow: hidden;
          background: #f3f5f7;
          margin-top: 20px;
          padding: 20px;
          .course-img{
            img{
              width: 160px;
              height: 90px;
            }
          }
          .course-info{
            width: 60%;
            margin-left: 20px;
            text-align: left;
            font-size: 14px;
            color: #333;
            p{
              line-height: 30px;
              &.course-name{
                font-weight: bold;
                font-size: 15px;
              }
            }
          }
          .course-price{
            line-height: 90px;
            color: red;
            font-size: 16px;
          }
        }
      }
    }
    .pay-box{
        display: flex;
        width: 100%;
        align-items: center;
      .payinfo{
        width: 100%;
        margin: 20px 0 10px;
        p{
        text-align: left;
        line-height: 30px;
        }
        .amount{
          span{
            color: red;
            font-weight: bold;
            font-size: 16px;
          }
        }
      }
      .wechat{
        width: 50%;
        button{
          background: #00C800;
        }
      }
      .alipay{
        text-align: right;
        button{
          background: #00AAEE;
        }
      }
    }
    .wechat{
        width: 50%;
        button{
          background: #00C800;
        }
      }
      .alipay{
        text-align: right;
        button{
          background: #00AAEE;
        }
      }
    .notice{
      color: #FF9800;
      font-size: 12px;
      float: right;
    }
  }

.alipay-dialog{
      p{
        width: 100%;
        font-size: 20px;
      }
  }
  .dialog-container{
    text-align: center;
    width: 420px;
    padding-bottom: 30px;
    background: #fff;
    top: 30%;
    border-radius: 10px;
    left: 38%;
    z-index: 15;
    position: absolute;
    box-shadow: 0 2px 8px 2px rgba(0, 0, 0, 0.1);
    .alipay-navbar{
     height: 50px;
      .dialog-navbar{
        border-bottom: 1px solid #d0d6d9;
        width: 90%;
        ul{
          display: flex;
          li{
            flex: 1;
            height: 50px;
            line-height: 50px;
            &.active{
              color: red;
              border-bottom: 1px solid red;
            }
          }
        }
      }
      .CloseBtn{
          position: absolute;
          right: 10px;
          cursor: pointer;
          top: 10px;
          z-index: 15;
      }
    }
    .payment-container{
      ul{
        li{
         .wechat-box{
            margin-top: 30px;
            img{
              width: 200px;
              height: 200px;
            }
          }
        p{
          margin-top: 12px;
          font-size: 20px;
        }
          .price{
            font-size: 17px;
          span{
              color:red;
              font-weight: bold;
            }
          }
          .AlypayText{
            color:#00AAEE
          }
          .WechatText{
            color: #00cb00;
          }
        }
      }
    }
  }

</style>



