<template>
 <div class="details">
    <Mheader></Mheader>
    <div class="details-bg warpper-box">
        <p class="course-name mw">vue2.0仿京东商场实战课程</p>
        <div class="details-content">
            <div class="details-info">
                <h1>全网首发仿京东实战课程</h1>
                <h2>vue2.0最新技术开发</h2>
                <p class="video-times">
                    <span>视频时长：1个小时20分钟</span>
                    <span>参与人数：1000人</span>
                </p>
                <div class="price">￥299.99</div>
                <div class="btn">
                    <button>
                        <router-link to="sureorder">立即购买</router-link>
                        <router-link to="cart"><i class="iconfont icon-msnui-cart"></i></router-link>
                    </button>
                </div>
            </div>
        </div>
    </div>
    <div class="video-box ov">
        <div class="video-btn fl">
            <div class="playbtn" @click="PlayVideo"><i class="iconfont icon-bofang"></i></div>
            <p>观看导学视频</p>
        </div>
        <div class="video-info fl">
            <h1>玩转数据结构 从入门到进阶</h1>
            <p>从数据结构基础到二叉树、红黑树、哈希表，bobo老师精心设计本课程，详细生动的为你讲解数据结构。让你面对数据结构可以学的会、玩的溜。掌握数据结构，完成从“搬砖”到“盖楼”的蜕变，就在此课。各位小伙伴不要错过！</p>
        </div>
        <div class="teachers-info fr">
            <div class="user-img"><img src="../assets/userlogo.png" alt=""></div>
            <div class="user-name">请叫我小任性</div>
            <p>算法大牛 ACM亚洲区奖牌获得者</p>
        </div>
    </div>
    <div class="dialog" v-show="VideoModel">
            <div class="dialog_bg"></div>
            <div class="dialog_content">
               
            <div class="closebtn" @click="CloseBtn">
                <i class="iconfont icon-delete"></i>
            </div>
            <video width="100%" height="100%" controls="controls">
                     <source src="http://v3.mukewang.com/shizhan/5ad05984e520e5a81c8b4739/H.mp4" type="video/ogg" />
                </video>
            </div>
    </div>
 </div>
</template>

<script type="text/javascript">
import Mheader from '@/components/Mheader'

  export default {
   data() {
     return {
         VideoModel:false
      }
  },
   components: {
     Mheader
   },
   methods:{
       PlayVideo(){
           this.VideoModel = true;
       },
       CloseBtn(){
           this.VideoModel = false;
       }
   }
 }
</script>

<style scoped lang="less">
h1,h2{
    font-weight: normal;
    margin-bottom: 30px;
}
button{
    border: 0;
    background: red;
    height: 38px;
    line-height: 38px;
    border-radius: 20px;
    margin-top: 30px;
    width: 150px;
    color: #fff;
    cursor: pointer;
    span{
        border-right: 1px solid #c3c3c3;
        padding-right: 4px;
    }
}
 .details-bg{
     background: url(../assets/2.jpg)center top no-repeat;background-size: cover;
     height: 500px;
     color: #fff;
    .course-name{
        padding-top: 40px;
        text-align: left;
    }
        .details-content{
            width: 30%;
            margin: 0 auto;
            .details-info{
            width: 100%;
            text-align: center;
            .video-times{
                span{
                    margin-right: 50px;
                }
            }
            .price{
                color: red;
                font-weight: 540;
                font-size: 24px;
                margin-top: 30px;
            }
        }
    }
 }
.video-box{
    width: 1150px;
    margin:  50px auto;
    border-radius: 20px;
    background: #000;
    // opacity: 0.9;
    background: url(../assets/bg.jpg)no-repeat;
    height: 500px;
    width: 900px;
    color: #fff;
    display: flex;
    align-items: center;
    .video-btn{
        width: 26%;
        text-align: center;
        .playbtn{
            margin-bottom: 10px;
            i{
                font-size: 80px;
            }
        }
        p{

        }
    }
    .video-info{
        width: 40%;
        text-align: left;
    }
    .teachers-info{
        width: 34%;
        text-align: center;
        .user-img{
            img{
            border-radius: 50%;
            border: 5px solid#fff;
            }
        }
        .user-name{

        }
    }
 }
 

 .dialog_content{    
    z-index: 15;
    width: 800px;
    height: 490px;
    position: fixed;
    top: 25%;
    left: 25%;
     .closebtn{
     position: absolute;
     z-index: 15;
     right: -4%;
     top: -4%;
     z-index: 3;
     i{
     color: #fff;
     font-size: 30px;
     }
 }
 }
</style>



