// 引入mockjs
const Mock = require('mockjs');
// 获取 mock.Random 对象
const Random = Mock.Random;
// mock一组数据
const produceNewsData = function() {
    let articles = [];
    for (let i = 0; i < 6; i++) {
        let newArticleObject = {
            class_name: Random.cword(4), // 随机四位数汉字
            course_name: Random.csentence(5, 30), //  Random.csentence( min, max )
            course_img: Random.dataImage('200x170', 'mock的图片'), // Random.dataImage( size, text ) 生成一段随机的 Base64 图片编码
            adv_img: Random.dataImage('200x470', 'mock的图片'), // Random.dataImage( size, text ) 生成一段随机的 Base64 图片编码
            course_price: Random.natural(60, 100), // Random.course_price() 随机生成一个常见的中文姓名
            course_number: Random.natural(60, 100), // Random.course_number() 随机生成参与人数
        }
        articles.push(newArticleObject)
    }
    return {
        articles: articles
    }
}

// Mock.mock( url, post/get , 返回的数据)；
Mock.mock('/news/index', 'post', produceNewsData);


