<template>
 <div class="lector">
     <Mheader></Mheader>
    <div class="warpper-box">
        <div class="mw content">
            <h2>讲师招募</h2>
            <p>如何能成为码学的讲师呢？</p>
            <p>我们希望你：</p>
            <ul>
                <li>讲师至少发起过一个课程；</li>
                <li>热衷分享；</li>
                <li>有3年以上的大型项目开发经验；</li>
                <li>至少精通前端开发技术、JAVA、Python、大数据开发、go语言开发、移动端开发，软件测试、UI设计中的一项；</li>
                <ol></ol>
            </ul>
            <button @click="Add">立即加入</button>
        </div>
   </div>
 </div>
</template>

<script type="text/javascript">
import Mheader from '@/components/Mheader'

export default {
   data() {
     return {

      }
  },
  components:{
      Mheader
  },
  methods:{
      Add(){
          var _this = this
            _this.$router.push({
                path:'/about/apply'
            })
      }
  }
 }
</script>

<style scoped lang="less">
.content{
  background: #fff;
  box-shadow: 0 2px 8px 2px rgba(0, 0, 0, 0.1);
  padding: 20px 30px 60px 30px;
  text-align: left;
  ul{
      margin-left: 30px;
      li{
          list-style-type : disc;
          line-height: 30px;
          font-size: 12px;
          color: #666;
      }
  }
  }
.mw{
    width: 1000px;
    margin: 150px auto;
}
h2{
    font-weight: normal;
    font-size: 18px;
    margin-bottom: 10px;
    padding-bottom: 14px;
    border-bottom: 1px solid #BBBBBB;
}
button{
    border: 1px solid #ff7413;
    width: 100px;
    margin-left: 20px;
    height:34px;
    border-radius: 4px;
    font-size: 12px;
    color: #fff;
    cursor: pointer;
    background: #fff;
    margin-top: 20px;
    background: #ff7413;
}
p{
    line-height: 30px;
}
</style>



