<template>
 <div class="page">
    <Mheader></Mheader>
    <div class="warpper-box">
        <div class="mw content">
            <h2>欢迎加入讲师团队，世界因你而精彩</h2>
            <dl>
                <dd style="vertical-align: top;">我的简介</dd>
                <dt><textarea name="" id="" cols="78" rows="10"></textarea></dt>
            </dl>
            <dl>
                <dd>我的姓名</dd>
                <dt><input type="text" placeholder="请输入你的姓名"></dt>
            </dl>
            <dl>
                <dd>联系方式</dd>
                <dt><input type="text" placeholder="请输入你的联系方式"></dt>
            </dl>
            <dl>
                <dd>Github</dd>
                <dt><input type="text" placeholder="请输入你的github账号"></dt>
            </dl>
        </div>
    </div>
 </div>
</template>

<script type="text/javascript">
import Mheader from '@/components/Mheader'

export default {
   data() {
     return {

      }
  },
   components: {
       Mheader
   }
 }
</script>

<style scoped lang="less">
.content{
  background: #fff;
  box-shadow: 0 2px 8px 2px rgba(0, 0, 0, 0.1);
  padding: 20px 30px 60px 30px;
  text-align: left;
}
.mw{
    width: 1000px;
    margin: 150px auto;
}
textarea{
resize:none;
    padding-left: 10px;
    padding-top: 10px;
border: 1px solid #bbb;
}
dd,dt{
    display: inline-block;
}

dd{
    margin-right: 10px;
    width: 100px;
    text-align: right;
}
dl{
    width: 100%;
    margin-top: 30px;
}
input{
    height: 30px;
    border: 1px solid #bbb;
    width: 566px;
    padding-left: 10px;
}
</style>



